header {
  display: flex;
  background-color: transparent;
  z-index: 2;
  /*width: 100vw;*/
  font-size: large;
  justify-content: space-between;
  transition: all 0.6s;
}

header svg {
  cursor: pointer;
}

header ul {
  width: 60%;
  list-style: none;
  flex-direction: row;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

header ul li {
  display: flex;
  flex-direction: row;
}

.header-item.button {
  order: 2;
  padding: 9px 5px;
}
.header-item {
  order: 3;
  padding: 2px;
  width: 100%;
  text-align: center;
  display: none;
}
header a {
  color: white;
  text-decoration: none;
}
header .in {
  display: flex;
}
header .in a button {
  color: white;
  margin: 10px;
  font-size: large;
  padding: 5px;
  border: none;
  cursor: pointer;
}

header ul li:hover {
  transform: scale(1.1);
  box-shadow: 0 9px 47px 11px rgba(51, 51, 51, 0.18);
  cursor: pointer;
}

nav {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
  background-color: rgb(11, 180, 166);
}

nav img {
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.in a button:first-of-type {
  background-color: transparent;
}
.in a button:first-of-type:hover {
  color: #e5a00d;
}
.in a button:nth-child(2):hover {
  background-color: #d89400;
}
.in a button:nth-child(2) {
  background-color: #e5a00d;
  border-radius: 20px;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  list-style-type: none;
}
.menu li a {
  display: block;
  padding: 5px 5px;
}

.toggle {
  order: 1;
  outline: none;
  width: 20px;
  font-size: 20px;
  color: black;
}

.active .header-item {
  display: block;
}
.n-active {
  position: absolute;
  height: 100vh;
  display: flex;
  align-items: flex-start;
}
.n-inactive {
  height: max-content;
  position: relative;
  display: flex;
  align-items: center;
  padding-inline: 20px;
}
.toggle {
  position: absolute;
  right: 30px;
  top: 20px;
}

@media (min-width: 800px) {
  .menu {
    display: flex;
    flex-wrap: nowrap;
  }
  .toggle {
    display: none;
  }
  .header-item {
    display: flex;
    width: max-content;
    margin: 0 5px;
  }
  .menu {
    display: flex;
  }
  nav {
    padding: 5px;
  }
}